import axios from "axios";
export default {
	namespaced: true,
	state: {
		refBrandItems: [],
	},
	mutations: {
		setRefBrandItems(state, refBrandItems) {
			state.refBrandItems = refBrandItems;
		},
	},
	getters: {
		_brandItems(state) {
			return state.refBrandItems;
		},
	},
	actions: {
		async getRefBrandItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_BRAND_REF;
			try {
				const response = await axios.get(apiUrl); // axios doğrudan kullanılıyor
				commit("setRefBrandItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
