import axios from "axios";
export default {
	namespaced: true,
	state: {
		withUsItems: [],
	},
	mutations: {
		setWithUsItems(state, withUsItems) {
			state.withUsItems = withUsItems;
		},
	},
	getters: {
		_withUsItems(state) {
			return state.withUsItems;
		},
	},
	actions: {
		async getWithUsItems({ commit }) {
			const apiUrl = process.env.VUE_APP_BASE_URL + process.env.VUE_APP_WITH_US;
			try {
				const response = await axios.get(apiUrl);
				commit("setWithUsItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
