<template>
	<div>
		<transition name="fade">
			<div v-if="loading" class="loadingContainer">
				<div class="loading">
					<div class="logoDiv">
						<img src="@/assets/image/top/logo/logo.svg" alt="" />
					</div>
					<div>
						<span> Yükleniyor:</span> <span>{{ loadingPercentage }}%</span>
					</div>
				</div>
				<div class="progress-container">
					<progress
						:value="loadingPercentage"
						max="100"
						class="progress"></progress>
				</div>
			</div>
		</transition>
		<div v-show="!loading">
			<div>
				<headerContainer />
			</div>
			<div>
				<mainContainer />
			</div>
			<div>
				<footerContainer />
			</div>
		</div>
	</div>
</template>

<style scoped>
	.logoDiv {
		display: flex;
		width: 160%;
	}
	.logoDiv img {
		object-fit: contain;
		width: 100%;
	}
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 2s;
	}
	.fade-enter-from,
	.fade-leave-to {
		opacity: 0;
	}
	.fade-enter-to,
	.fade-leave-from {
		opacity: 1;
	}
	.loading {
		display: flex;
		flex-direction: column;
		row-gap: 67px;
		align-items: center;
	}
	.loadingContainer span {
		display: flex;
		min-width: 150px;
		justify-content: center;
	}
	.loadingContainer {
		row-gap: 28px;
		font-size: 50px;
		width: 100%;
		height: 100%;
		position: fixed;
		display: flex;
		color: white;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		z-index: 9999;
		background-color: black;
	}
	.loading-bar {
		text-align: center;
		padding: 20px;
		color: white;
	}

	.progress-container {
		display: flex;
		width: 30%;
		height: 30px;
		border-radius: 10px;
		overflow: hidden;
	}

	.progress {
		width: 100%;
		height: 100%;
		transition: width 0.5s;
		background-color: rgb(0, 0, 0);
	}

	.loading-text {
		margin-top: 10px;
	}
	progress::-webkit-progress-bar {
		background-color: black;
		border: 1px solid rgba(149, 11, 11, 0.657);
		border-radius: 10px;
	}
	progress[value]::-webkit-progress-value {
		background-image: -webkit-linear-gradient(
				-45deg,
				transparent 33%,
				rgba(0, 0, 0, 0.1) 33%,
				rgba(0, 0, 0, 0.1) 66%,
				transparent 66%
			),
			-webkit-linear-gradient(top, rgba(255, 255, 255, 0.25), rgba(0, 0, 0, 0.25)),
			-webkit-linear-gradient(left, rgb(0, 0, 0), rgb(187, 0, 0));

		border-radius: 10px;
		background-size: 50px 30px, 100% 100%, 100% 100%;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
	}
</style>
<script>
	import mainContainer from "./components/main/mainContainer.vue";
	import headerContainer from "./components/top/headerContainer.vue";
	import footerContainer from "./components/footer/footerContainer.vue";
	export default {
		data() {
			return {
				loading: true,
				loadingPercentage: 0,
			};
		},
		mounted() {
			this.simulateLoading();
		},
		methods: {
			simulateLoading() {
				const increment = 1;
				const totalSteps = 100;
				const stepDuration = 50;

				const interval = setInterval(() => {
					if (this.loadingPercentage < 100) {
						this.loadingPercentage += increment;
					} else {
						clearInterval(interval);
						this.loading = false;
					}
				}, stepDuration);

				setTimeout(() => {
					clearInterval(interval);
					this.loading = false;
				}, totalSteps * stepDuration);
			},
		},

		components: {
			headerContainer,
			mainContainer,
			footerContainer,
		},
	};
</script>
